.record {
    display: flex;
    flex-direction: row;
    outline: none;
    border: none;
    padding-top: 5px;
    padding-bottom: 5px;
    padding-left: 5px;
    border-top: 1px solid rgba(0, 0, 0, 0.2);
}

.record.filler, .record.filler * {
    pointer-events: none;
    opacity: 0;
}

@keyframes disappear {
    from {

        opacity: 1;
        background: transparent;
    }
    to {

        opacity: 0;
        background: #90ff90;
    }
}

.record.accomplished {
    animation: 0.3s disappear ease-in-out;
    background: #90ff90;
    transform: translateX(-100%);
    opacity: 0;
    outline: none;
    border: none;
}
