.nav__button {
    border: none;
    outline: none;
    font-weight: bold;
    box-shadow: rgba(99, 99, 99, 0.2) 0px 2px 8px 0px;
    border-radius: 5px;
    padding: 7px;
    transition: 0.1s ease-in-out;
    background-color: rgba(255, 255, 255, 0.4);

}

.nav__button:hover {
    background-color: rgba(0, 0, 0, 0);
}