.reminder {
    display:flex;
    flex-direction: column;
    background-color: white;


    width: 300px;
    height: 300px;
    animation: 0.2s ease-in-out appear;
    border-radius: 8px;
    box-shadow: rgba(0, 0, 0, 0.24) 0px 3px 8px;
    overflow: hidden;
    overflow-y: scroll;
    position: relative;
}

.reminder::-webkit-scrollbar {
    width: 0;
}

.reminder-control {
    display: flex;
    flex-direction: row;
    padding: 5px;
    position: fixed;
    width: 290px;
    border-top-left-radius: 8px;
    border-top-right-radius: 8px;
    background-image: linear-gradient(135deg, rgba(245, 247, 250, 0.4) 0%, rgba(195, 207, 226, 0.4) 100%);
    margin-bottom: 50px;
    -webkit-backdrop-filter: blur(5px);
    backdrop-filter: blur(5px);
}

.reminder-control > input {
    font-size: 18px;
    font-weight: bold;
    min-width: 0px;
}
.reminder-control > button:last-child {
    margin-left: 5px;
}

@keyframes appear {
    from {
        transform: scale(0);
        opacity: 0;
    }
    to {
        transform: scale(1);
        opacity: 1;
    }
}

@keyframes rem-disappear {
    to {
        transform: scale(0);
        opacity: 0;
    }
    from {
        transform: scale(1);
        opacity: 1;
    }
}

.reminder.closing {
    animation: 0.2s ease-in-out rem-disappear;
    opacity: 0;
}
